$portSize: 1.1rem;
$portLabelFontSize: 0.9rem;
$portLabelFontWeight: 600;
$portLabelVerticalBump: 20%;
$nodeBackground: #ffffff;
$portColor: #323135;
$contentFontSize: 1rem;
$condButtonFontSize: 0.9rem;

// IF BLOCK
$ifBackground: #6ca4e1;

// WHILE BLOCK
$whileBackground: #7fe16c;


.special-node {
    background: $nodeBackground;
    border-radius: 0.5em;
    border-color: white;
    border-width: 2px;
    border-style: solid;

    .InputPortDiv {
        .port {
            width: $portSize;
            height: $portSize;
            background: $portColor;
            border-radius: 50%;
            display: inline-block;
        }
    }

    .ports {
        display: flex;
        justify-content: space-between;
        label {
            display: inline-block;
            vertical-align: $portLabelVerticalBump;
            margin-right: 3px;
            margin-left: 3px;
            font-size: $portLabelFontSize;
            font-weight: $portLabelFontWeight;
            color: $portColor;
        }

        .inputs {
            align-items: center;
            justify-content: center;
            padding: 0.5rem;
        }

        .content {
            align-self: center;
            font-weight: 800;
            font-size: $contentFontSize;
            margin-left: 0.5em;
            margin-right: 0.5em;
            display: flex;
            flex-direction: column;
            margin-bottom: 0.3em;
        }
    }

     .buttons {
        flex-direction: row;
        
        display: flex;
        justify-content: center;
        button {
            border-radius: 0.5em;
            font-size: $condButtonFontSize;
            align-self: center;
            font-weight: 600;
            &:hover {
                font-weight: 700;
            }
        }

        .true-btn:hover {
            background-color: rgb(23, 193, 74);
        }
    }

    button {
        border-radius: 50%;
        padding: 0.3em;
        border-style: solid;
        border-color: transparent;
        &:hover {
            background-color: rgb(199, 45, 45);
            color: white;
            transform: scale(1);
            border-color: white;
        }
    }

    .deleteBtn {
        display: none;
        position: absolute;
        right: -15px;
        top: -15px;
    }

    &:hover .deleteBtn {
        display: block;
    }
}


.if-node {
    @extend .special-node;
    background: $ifBackground;
}

.while-node {
    @extend .special-node;
    background: $whileBackground;
}

