$portSize: 1.1rem;
$portLabelFontSize: 0.9rem;
$portLabelFontWeight: 600;
$portLabelVerticalBump: 20%;
$nodeBackground: #7c7cf3;
$portColor: #323135;
$contentFontSize: 1rem;

.custom-node {
    background: $nodeBackground;
    border-radius: 0.5em;
    border-color: white;
    border-width: 2px;
    border-style: solid;
    padding-top: 0.7em;
    padding-bottom: 0.3em;
    .InputPortDiv {
        .port {
            width: $portSize;
            height: $portSize;
            background: $portColor;
            border-radius: 50%;
            display: inline-block;
        }
    }

    .OutputPortDiv {
        .port {
            width: $portSize;
            height: $portSize;
            background: $portColor;
            border-radius: 50%;
            display: inline-block;
        }
    }

    .ports {
        display: flex;
        justify-content: space-between;
        label {
            display: inline-block;
            vertical-align: $portLabelVerticalBump;
            margin-right: 3px;
            margin-left: 3px;
            font-size: $portLabelFontSize;
            font-weight: $portLabelFontWeight;
            color: $portColor;
        }

        .inputs {
            align-items: center;
            justify-content: center;
            padding: 0.5rem;
        }

        .content {
            align-self: center;
            font-weight: 800;
            font-size: $contentFontSize;
            margin-left: 0.5em;
            margin-right: 0.5em;
        }

        .outputs {
            display: block;
            padding: 0.5rem;
        }
    }

    button {
        border-radius: 50%;
        padding: 0.3em;
        border-style: solid;
        border-color: transparent;
        &:hover {
            background-color: rgb(193, 23, 23);
            color: white;
            transform: scale(1);
            border-color: white;
        }
    }

    .deleteBtn {
        display: none;
        position: absolute;
        right: -15px;
        top: -15px;
    }

    &:hover .deleteBtn {
        display: block;
    }
}

.custom-node-problem {
    @extend .custom-node;
    background-color: rgb(242, 104, 104);
    // border-width: 0.3em;
    // border-style: dashed;
    animation: spin 2s ease-in-out infinite;
}

@keyframes spin {
    0% {
        background-color: rgb(235, 55, 55);
    }
    
    50% {
        background-color: rgb(230, 56, 56);
    }
}
