html {
  background-color: #28282d;
}
// App
.App {
  width: 100%;
  height: 100%;
  z-index: -1;

  // CSS GRID
  .grid-container {
    display: grid;
    grid-template-columns: 70%;
    grid-template-rows: 1fr;
    gap: 0px 0px;
    grid-template-areas: "Ide Runtime";
    margin: 1em;

    .Ide {
      grid-area: Ide;
      margin: 0.4em;
    }

    .Runtime {
      grid-area: Runtime;
      margin: 0.4em;
    }
  }
  
  h1{
    color:white;
    margin-left: 25px;
  }
}

button {
  padding: 2em;
  margin: 0.5em;
  border-radius: 1em;
  border-style: none;
  transition: all 0.1s ease-in-out;

  &:hover {
    transform: scale(1.025);
  }
}
