
.diagram-section {
  display: flex;
  flex-direction: row;
  margin-bottom: 0.5em;

  .diagram-display-area {
    width: 100%;
    height: 36rem;
    overflow: scroll;
    border-radius: 1em;
    -ms-overflow-style: none; /* IE and Edge*/
    scrollbar-width: none; /*  Firefox  */

    &::-webkit-scrollbar {
      display: none;
    }
  }

  .sidebar {
    border-radius: 1em;
    background: #1e1e24;
    width: 150px;
    margin-left: 0.5em;
    height: 35rem;
    overflow-y: auto;
    padding: 0.5em;

    button {
      width: 90%;
      padding: 1em;
      padding-top: 0.5em;
      padding-bottom: 0.5em;
      margin-bottom: -2px;
      border-radius: 0.3em;

      &:hover {
        background-color: #7c7cf3;
        color: white;
        font-weight: 600;
      }
    }
  }
}


.compile-btn {
  background-color: rgb(110, 165, 110);
  color: white;
  font-weight: 800;
  font-size: 0.8em;
}
