.runtime-section {
  border-radius: 20px;
  background: #1e1e24;
  padding: 1em;
  // border-color: "white";
  // border-width: "medium";
  // border-style: "solid";
  display: flex;
  flex-direction: column;

  .runtime-output {
    height: 34rem;
    background-color: #4f4f54;
    border-radius: 15px;
    position: relative;
    
    .controller {
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;


      .joystick-demo {
        position: absolute;
        left: 0;
        bottom: 0;
      }
    }
  }
}
